*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  text-decoration: none;
  color: inherit;
}

button {
  border: none;
  background: none;
  cursor: pointer;
  font: inherit;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

body {
  font-family: "Sora", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  background-color: #12121c;
  color: #ffffff;
}

.page {
  min-height: 100vh;
}

.no-scroll {
  overflow: hidden;
}

.container {
  width: 100%;
  margin-inline: auto;
  padding-inline: 16px;
}
@media (min-width: 414px) {
  .container {
    padding-inline: 32px;
  }
}
@media (min-width: 768px) {
  .container {
    padding-inline: 60px;
  }
}
@media (min-width: 1024px) {
  .container {
    padding-inline: 90px;
  }
}
@media (min-width: 1920px) {
  .container {
    padding-inline: 120px;
  }
}

.section {
  padding-block: 48px;
}
@media (min-width: 768px) {
  .section {
    padding-block: 64px;
  }
}
@media (min-width: 1024px) {
  .section {
    padding-block: 80px;
  }
}
@media (min-width: 1440px) {
  .section {
    padding-block: 96px;
  }
}

.header {
  background-color: #12121c;
  padding-block: 27px;
}
.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header__logo {
  display: inline-flex;
  align-items: center;
  gap: 12px;
  max-width: 115px;
  margin-left: 9px;
}
.header__logo-icon {
  width: 23px;
  height: 28px;
  flex-shrink: 0;
}
.header__logo-text {
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 22px;
}
.header__burger {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  transition: transform 0.25s ease, background-color 0.25s ease;
  border-radius: 8px;
  padding: 4px;
}
.header__burger-icon {
  width: 26px;
  height: 18px;
  transition: transform 0.25s ease, opacity 0.25s ease;
}
.header__burger:active {
  transform: scale(0.94);
}
.header__burger--active {
  background-color: rgba(255, 255, 255, 0.06);
  transform: rotate(90deg);
}
.header__burger--active .header__burger-icon {
  transform: scale(1.05);
}
.header__nav {
  display: none;
}
.header__nav-list {
  display: flex;
  align-items: center;
  gap: 40px;
  margin-right: 40px;
}
.header__nav-link {
  display: inline-block;
  font-weight: 700;
  font-size: 14px;
  color: #88888d;
  transition: color 0.2s ease;
}
.header__nav-link:hover, .header__nav-link:focus-visible {
  color: #ffffff;
}
.header__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 14px 24px;
  border-radius: 8px;
  background: #222233;
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 14px;
  color: #ffffff;
}
@media (min-width: 768px) {
  .header__burger {
    display: none;
  }
  .header__nav {
    display: flex;
    align-items: center;
    margin-left: auto;
  }
}
@media (min-width: 1024px) {
  .header {
    padding-block: 20px;
  }
  .header__logo {
    gap: 17px;
    max-width: 141px;
  }
  .header__logo-icon {
    width: 27px;
    height: 33px;
  }
  .header__logo-text {
    font-size: 27px;
  }
}
@media (min-width: 1440px) {
  .header {
    padding-top: 26px;
    padding-bottom: 22px;
  }
}
@media (min-width: 1920px) {
  .header {
    padding-top: 32px;
    padding-bottom: 28px;
  }
  .header__logo {
    gap: 20px;
    max-width: none;
  }
  .header__logo-icon {
    width: 32px;
    height: 40px;
  }
  .header__logo-text {
    font-size: 30px;
  }
  .header__nav-list {
    gap: 48px;
    margin-right: 60px;
  }
  .header__nav-link {
    font-size: 18px;
  }
  .header__btn {
    font-size: 18px;
    padding: 16px 32px;
    border-radius: 10px;
  }
}

.mobile-menu {
  position: fixed;
  inset: 0;
  z-index: 1000;
  display: block;
  background-color: rgba(5, 5, 15, 0.98);
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transform: translateY(-8px);
  transition: opacity 0.45s cubic-bezier(0.22, 0.61, 0.36, 1), transform 0.45s cubic-bezier(0.22, 0.61, 0.36, 1), visibility 0.45s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.mobile-menu--open {
  opacity: 1;
  visibility: visible;
  pointer-events: auto;
  transform: translateY(0);
}
.mobile-menu__backdrop {
  position: absolute;
  inset: 0;
}
.mobile-menu__content {
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 24px 16px 32px;
  transform: translateY(12px);
  transition: transform 0.45s cubic-bezier(0.22, 0.61, 0.36, 1);
}
.mobile-menu--open .mobile-menu__content {
  transform: translateY(0);
}
.mobile-menu__close {
  align-self: flex-end;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.06);
  transition: background-color 0.2s ease, transform 0.2s ease;
}
.mobile-menu__close:hover, .mobile-menu__close:focus-visible {
  background-color: rgba(255, 255, 255, 0.12);
}
.mobile-menu__close:active {
  transform: scale(0.95);
}
.mobile-menu__nav {
  margin: 32px 0;
}
.mobile-menu__nav-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 24px;
}
.mobile-menu__nav-link {
  font-weight: 700;
  font-size: 18px;
  color: #ffffff;
  text-decoration: none;
}
.mobile-menu__btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  margin-top: 50px;
  padding-block: 14px;
  border-radius: 8px;
  font-family: "DM Sans", sans-serif;
  font-weight: 700;
  font-size: 16px;
  color: #ffffff;
  background: #222233;
  transition: background-color 0.2s ease, transform 0.2s ease;
}
.mobile-menu__btn:hover, .mobile-menu__btn:focus-visible {
  background-color: rgb(42.16, 42.16, 63.24);
}
.mobile-menu__btn:active {
  transform: scale(0.97);
}
@media (min-width: 768px) {
  .mobile-menu, .mobile-menu--open {
    display: none;
  }
}

.projects__container {
  padding-block: 45px;
}
.projects__item {
  margin-bottom: 50px;
}
.projects__item:last-child {
  margin-bottom: 0;
}
.projects__card {
  width: 100%;
  max-width: 350px;
  margin: 0 auto;
  border-radius: 8px;
  overflow: hidden;
}
.projects__card-link {
  position: relative;
  display: block;
}
.projects__card-link:focus-visible {
  outline: 2px solid rgba(255, 255, 255, 0.5);
  outline-offset: 4px;
  border-radius: 8px;
}
.projects__image {
  display: block;
  width: 100%;
  height: auto;
}
.projects__card-meta {
  position: absolute;
  left: 0;
  bottom: 0;
  width: calc(100% - 74px);
  max-width: 276px;
  display: flex;
  align-items: center;
  padding: 10px 15px;
  background-color: #222233;
  border-radius: 0 8px 0 0;
}
.projects__card-title {
  font-family: "Sora", sans-serif;
  font-weight: 600;
  font-size: 14px;
  line-height: 1.714;
  max-width: 246px;
}
.projects__card-title-suffix {
  white-space: nowrap;
}
.projects__card-btn {
  position: absolute;
  right: 15px;
  bottom: 15px;
  width: 44px;
  height: 44px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 8px;
  background-color: #222233;
  transition: background-color 200ms ease, transform 200ms ease, box-shadow 200ms ease;
}
.projects__card-btn--green {
  background-color: #57efb4;
}
.projects__card-btn--green .projects__card-btn-icon {
  stroke: #12121c;
}
.projects__card-btn:hover, .projects__card-btn:focus-visible {
  transform: translateY(-4px);
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.45);
}
.projects__card-btn-icon {
  width: 9px;
  height: 9px;
  stroke: #ffffff;
  transition: stroke 200ms ease, transform 200ms ease;
}
@media (min-width: 768px) {
  .projects__container {
    padding-block: 60px;
    padding-inline: 45px;
  }
  .projects__item {
    margin-bottom: 56px;
  }
  .projects__card {
    max-width: 640px;
  }
  .projects__card-meta {
    width: auto;
    max-width: 380px;
    padding: 12px 18px;
  }
  .projects__card-title {
    max-width: 330px;
  }
}
@media (min-width: 1024px) {
  .projects__container {
    padding-block: 100px 90px;
  }
  .projects__item {
    margin-bottom: 90px;
  }
  .projects__card {
    max-width: 844px;
  }
  .projects__card-meta {
    left: 30px;
    bottom: 30px;
    max-width: none;
    border-radius: 8px;
    padding: 20px;
  }
  .projects__card-title {
    max-width: none;
  }
  .projects__card-title-suffix {
    display: none;
  }
  .projects__card-btn {
    right: 30px;
    bottom: 30px;
  }
}
@media (min-width: 1440px) {
  .projects__container {
    padding-block: 120px;
  }
  .projects__item {
    margin-bottom: 80px;
  }
  .projects__card {
    max-width: 1260px;
  }
  .projects__card-meta {
    left: 40px;
    bottom: 40px;
  }
  .projects__card-btn {
    right: 40px;
    bottom: 40px;
  }
}
@media (min-width: 1920px) {
  .projects__container {
    padding-block: 160px;
  }
  .projects__item {
    margin-bottom: 90px;
  }
  .projects__card {
    max-width: 1260px;
  }
  .projects__card-title {
    font-size: 18px;
  }
  .projects__card-meta {
    padding: 30px;
    left: 50px;
    bottom: 50px;
  }
  .projects__card-btn {
    width: 60px;
    height: 60px;
    right: 50px;
    bottom: 50px;
  }
}

.cta {
  padding-block: 45px;
}
.cta__inner {
  width: 100%;
  max-width: 350px;
}
.cta__text {
  width: 100%;
  max-width: 100%;
  margin-bottom: 24px;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71429;
  color: #88888d;
}
.cta__button {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  min-width: 145px;
  min-height: 52px;
  padding: 14px 24px;
  border-radius: 8px;
  background-color: #006ada;
  font-family: "DM Sans", system-ui, -apple-system, BlinkMacSystemFont, "Segoe UI", sans-serif;
  font-weight: 700;
  font-size: 16px;
  line-height: 1.5;
  color: #ffffff;
  transition: background-color 0.2s ease, transform 0.2s ease;
}
.cta__button:hover {
  background-color: rgb(0, 91.1211009174, 187.4);
}
.cta__button:active {
  transform: translateY(1px);
}
.cta__button:focus-visible {
  outline: 2px solid rgba(255, 255, 255, 0.85);
  outline-offset: 3px;
}
.cta__title {
  width: 100%;
  max-width: 350px;
  margin-top: 60px;
  font-weight: 700;
  font-size: 32px;
  line-height: 1.25;
  letter-spacing: -0.01em;
  color: #ffffff;
}
@media (min-width: 415px) and (max-width: 767px) {
  .cta__inner, .cta__title {
    max-width: 100%;
  }
}
@media (min-width: 768px) {
  .cta {
    padding-block: 64px;
  }
  .cta__inner {
    max-width: 100%;
    display: grid;
    grid-template-columns: minmax(280px, 360px) minmax(0, 1fr);
    grid-template-areas: "text title" "button title";
    column-gap: 60px;
    row-gap: 24px;
    align-items: start;
  }
  .cta__text {
    grid-area: text;
    margin-bottom: 0;
    font-size: 16px;
    line-height: 1.5;
  }
  .cta__text br {
    display: none;
  }
  .cta__button {
    grid-area: button;
    justify-self: start;
  }
  .cta__title {
    grid-area: title;
    margin-top: 0;
    width: 100%;
    max-width: 271px;
    font-size: 40px;
    line-height: 1.2;
  }
}
@media (min-width: 769px) and (max-width: 1023px) {
  .cta__inner {
    grid-template-columns: minmax(280px, 360px) minmax(0, 1fr);
  }
  .cta__title {
    max-width: 100%;
  }
}
@media (min-width: 1024px) {
  .cta {
    padding-block: 110px;
  }
  .cta__inner {
    grid-template-columns: 271px minmax(0, 1fr);
    column-gap: 65px;
    row-gap: 24px;
  }
  .cta__text {
    max-width: 271px;
    font-size: 14px;
    line-height: 1.71429;
  }
  .cta__title {
    max-width: 663px;
    font-size: 48px;
    line-height: 1.16667;
    letter-spacing: -0.02em;
  }
}
@media (min-width: 1025px) and (max-width: 1439px) {
  .cta__inner {
    grid-template-columns: 271px minmax(0, 1fr);
  }
  .cta__title {
    max-width: 100%;
  }
}
@media (min-width: 1440px) {
  .cta {
    padding-block: 120px;
  }
  .cta__inner {
    width: 100%;
    max-width: 100%;
    grid-template-columns: minmax(0, 530px) minmax(0, 508px);
    column-gap: 222px;
    row-gap: 0;
  }
  .cta__text {
    margin-top: 20px;
    max-width: none;
    font-size: 14px;
    line-height: 1.71429;
  }
  .cta__text br {
    display: inline;
  }
  .cta__title {
    width: 100%;
    max-width: 508px;
    font-size: 48px;
    line-height: 1.16667;
    letter-spacing: -0.02em;
  }
}
@media (min-width: 1441px) and (max-width: 1919px) {
  .cta__inner {
    grid-template-columns: minmax(0, 530px) minmax(0, 508px);
    column-gap: 222px;
  }
}
@media (min-width: 1920px) {
  .cta {
    padding-block: 140px;
  }
  .cta__inner {
    grid-template-columns: minmax(0, 600px) minmax(0, 560px);
    column-gap: 320px;
  }
  .cta__text {
    margin-top: 24px;
    font-size: 16px;
    line-height: 1.6;
  }
  .cta__button {
    min-width: 170px;
    min-height: 56px;
    padding: 16px 28px;
    font-size: 18px;
    border-radius: 10px;
  }
  .cta__title {
    max-width: 560px;
    font-size: 56px;
    line-height: 1.15;
    letter-spacing: -0.02em;
  }
}

.footer {
  padding-block: 55px;
  background-color: #222233;
}
.footer__inner {
  display: flex;
  flex-direction: column;
}
.footer__logo {
  display: inline-flex;
  align-items: center;
  gap: 16px;
  margin-left: 9px;
  margin-bottom: 52px;
  font-family: "Sora", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 1.1;
  letter-spacing: -0.01em;
  color: #ffffff;
}
.footer__logo:focus-visible {
  outline: 2px solid rgba(255, 255, 255, 0.5);
  outline-offset: 4px;
  border-radius: 8px;
}
.footer__logo-icon {
  flex: 0 0 auto;
}
.footer__logo-text {
  display: inline-block;
  letter-spacing: -0.05em;
}
.footer__links {
  display: grid;
  grid-template-columns: repeat(2, minmax(0, 1fr));
  grid-template-rows: repeat(4, auto);
  grid-auto-flow: column;
  column-gap: 32px;
  row-gap: 8px;
  margin-bottom: 48px;
}
.footer__link {
  display: inline-flex;
  font-family: "Sora", sans-serif;
  font-weight: 400;
  font-size: 14px;
  line-height: 1.71429;
  color: #ffffff;
  transition: opacity 0.2s ease;
}
.footer__link:hover {
  opacity: 0.8;
}
.footer__link:focus-visible {
  outline: 2px solid rgba(255, 255, 255, 0.5);
  outline-offset: 4px;
  border-radius: 6px;
}
.footer__bottom {
  display: flex;
  flex-direction: column;
  gap: 24px;
}
.footer__copyright {
  font-family: "Inter", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #88888d;
}
.footer__social {
  display: flex;
  align-items: center;
  gap: 16px;
}
.footer__social-link {
  --size: 32px;
  --icon: 20px;
  width: var(--size);
  height: var(--size);
  display: inline-flex;
  align-items: center;
  justify-content: center;
  border-radius: 999px;
  background-color: transparent;
  color: #ffffff;
  transition: background-color 0.2s ease, color 0.2s ease, transform 0.2s ease, box-shadow 0.2s ease, opacity 0.2s ease;
  will-change: transform;
}
.footer__social-link:hover {
  background-color: rgba(255, 255, 255, 0.16);
  color: #57efb4;
  transform: translateY(-1px) scale(1.06);
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.28);
}
.footer__social-link:active {
  transform: translateY(0) scale(1.02);
  opacity: 0.96;
}
.footer__social-link:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.55), 0 10px 24px rgba(0, 0, 0, 0.28);
}
.footer__social-link--active {
  background-color: #57efb4;
  color: #222233;
  box-shadow: 0 10px 24px rgba(0, 0, 0, 0.28);
}
.footer__social-link--active:hover {
  background-color: rgb(59.0608695652, 236.3391304348, 167.527173913);
  color: #222233;
  transform: translateY(-1px) scale(1.06);
  box-shadow: 0 0 0 3px rgba(87, 239, 180, 0.35), 0 14px 30px rgba(0, 0, 0, 0.34);
}
.footer__social-link--active:focus-visible {
  outline: none;
  box-shadow: 0 0 0 3px rgba(255, 255, 255, 0.6), 0 0 0 6px rgba(87, 239, 180, 0.35), 0 14px 30px rgba(0, 0, 0, 0.34);
}
.footer__social-icon {
  width: var(--icon);
  height: var(--icon);
  fill: currentColor;
  stroke: currentColor;
}
@media (min-width: 768px) {
  .footer__links {
    grid-template-columns: repeat(4, minmax(0, 1fr));
    grid-template-rows: repeat(2, auto);
    grid-auto-flow: row;
  }
}
@media (min-width: 1024px) {
  .footer {
    padding-block: 100px;
  }
  .footer__logo {
    margin-left: 10px;
    margin-bottom: 32px;
  }
  .footer__link-item:nth-child(1) {
    grid-column: 1;
    grid-row: 1;
  }
  .footer__link-item:nth-child(3) {
    grid-column: 1;
    grid-row: 2;
  }
  .footer__link-item:nth-child(5) {
    grid-column: 2;
    grid-row: 1;
  }
  .footer__link-item:nth-child(7) {
    grid-column: 2;
    grid-row: 2;
  }
  .footer__link-item:nth-child(2) {
    grid-column: 3;
    grid-row: 1;
  }
  .footer__link-item:nth-child(4) {
    grid-column: 3;
    grid-row: 2;
  }
  .footer__link-item:nth-child(6) {
    grid-column: 4;
    grid-row: 1;
  }
  .footer__bottom {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 0;
  }
  .footer__social {
    gap: 24px;
  }
}
@media (min-width: 1440px) {
  .footer {
    padding-block: 125px;
  }
  .footer__logo {
    margin-bottom: 38px;
  }
}
@media (min-width: 1920px) {
  .footer {
    padding-block: 140px;
  }
  .footer__logo {
    gap: 22px;
    font-size: 32px;
    margin-left: 0;
    margin-bottom: 64px;
  }
  .footer__logo-icon {
    transform: scale(1.1);
    transform-origin: left center;
  }
  .footer__links {
    column-gap: 48px;
    row-gap: 14px;
    margin-bottom: 64px;
  }
  .footer__link {
    font-size: 16px;
    line-height: 1.6;
  }
  .footer__bottom {
    gap: 32px;
  }
  .footer__copyright {
    font-size: 18px;
    line-height: 1.6;
  }
  .footer__social {
    gap: 32px;
  }
  .footer__social-link {
    --size: 40px;
    --icon: 24px;
  }
