// * ========== HERO ==========

.blog-hero {
  padding-block: 90px 45px;

  &__inner {
    width: 100%;
    max-width: clamp(350px, 78vw, 640px);
    margin-inline: auto;
    text-align: center;
  }

  &__title {
    font-weight: 700;
    font-size: 40px;
    line-height: 1.2;
    letter-spacing: -0.01em;
    margin-bottom: 16px;
  }

  &__text {
    font-weight: 400;
    font-size: 24px;
    line-height: 1.3;
    letter-spacing: -0.01em;
    color: $color-text-muted;
    margin-bottom: 48px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 8px;
  }

  &__input {
    width: 100%;
    min-height: 53px;
    padding: 14px 20px;

    border-radius: 8px;
    border: 1px solid rgba(255, 255, 255, 0.08);
    background-color: rgba(255, 255, 255, 0.03);

    color: $color-text-main;
    font-size: 14px;
    line-height: 1.7;
    letter-spacing: 0.7px;

    &::placeholder {
      color: rgba(255, 255, 255, 0.7);
    }

    &:focus-visible {
      outline: 2px solid rgba(255, 255, 255, 0.45);
      outline-offset: 3px;
    }
  }

  &__button {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    width: 100%;
    min-height: 52px;
    padding: 14px 24px;

    border-radius: 8px;
    background-color: $color-accent;
    color: #fff;

    font-family: "DM Sans", "Sora", sans-serif;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.5;

    transition: background-color 0.2s ease, transform 0.2s ease;

    &:hover {
      background-color: darken($color-accent, 6%);
    }

    &:active {
      transform: translateY(1px);
    }

    &:focus-visible {
      outline: 2px solid rgba(255, 255, 255, 0.85);
      outline-offset: 3px;
    }
  }

  // 768px
  @media (min-width: $bp-tablet) {
    padding-block: 100px clamp(64px, 10vw, 101px);

    &__inner {
      max-width: 100%;
    }

    &__title {
      font-size: 56px;
      line-height: 1.12;
      margin-bottom: 16px;
    }

    &__text {
      font-size: 22px;
      line-height: 1.5;
      max-width: 60ch;
      margin-inline: auto;
      margin-bottom: 48px;
    }

    &__form {
      flex-direction: row;

      width: 100%;
      max-width: 702px;
      margin-inline: auto;
      gap: 8px;
    }

    &__input {
      flex: 1 1 auto;
      min-width: 0;

      border: 1px solid $color-surface;
      background-color: rgba(255, 255, 255, 0.02);
    }

    &__button {
      width: auto;
      flex: 0 0 auto;
      white-space: nowrap;
    }
  }

  // 1024px
  @media (min-width: $bp-laptop-md) {
    &__title {
      font-size: 64px;
      line-height: 1;
      letter-spacing: -0.02em;
    }

    &__text {
      font-size: 24px;
      line-height: 1.33333;
      max-width: 70ch;
    }
  }

  // 1440px
  @media (min-width: $bp-laptop) {
    padding-block: 120px;

    &__inner {
      max-width: 830px;
    }

    &__title {
      font-size: 72px;
      line-height: 1.1;
    }

    &__text {
      margin-bottom: 50px;
    }
  }

  // 1920px
  @media (min-width: $bp-desktop) {
    padding-block: 140px;

    &__inner {
      max-width: 900px;
    }
  }
}
// * ========== ARTICLES ==========

.blog-articles {
  &__container {
    display: flex;
    flex-direction: column;
  }

  &__header {
    max-width: 60ch;
    margin-bottom: 48px;
  }

  &__title {
    font-weight: 700;
    font-size: 32px;
    line-height: 1.25;
    letter-spacing: -0.01em;
    color: $color-text-main;
    margin-bottom: 16px;
  }

  &__lead {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.71429;
    color: $color-text-muted;
  }

  &__list {
    display: grid;
    gap: 32px;
  }

  &__item {
    min-width: 0;
  }

  &__card {
    display: flex;
    flex-direction: column;

    border-radius: 8px;
    overflow: hidden;

    background-color: transparent;
    transition: background-color 200ms ease;

    &:hover {
      background-color: $color-surface;
    }

    &:focus-visible {
      outline: 2px solid rgba(255, 255, 255, 0.5);
      outline-offset: 6px;
    }

    &:hover .blog-articles__more {
      text-decoration: underline;
    }
  }

  &__media {
    width: 100%;
    min-height: 316px;
    height: 316px;
    overflow: hidden;
  }

  &__media picture {
    display: block;
    width: 100%;
    height: 100%;
  }

  &__img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }

  &__body {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 20px;
  }

  &__card-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 1.33333;
    color: $color-text-main;

    max-width: 18ch;
    margin-bottom: 16px;
  }

  &__excerpt {
    font-weight: 400;
    font-size: 14px;
    line-height: 1.71429;
    color: $color-text-muted;

    max-width: 34ch;
  }

  &__more {
    display: inline-flex;
    align-items: center;
    gap: 12px;
    margin-top: 24px;

    font-weight: 600;
    font-size: 14px;
    line-height: 1.71429;
    color: $color-accent;
  }

  &__icon {
    flex: 0 0 auto;
  }

  // &__item:nth-child(2) &__card {
  //   background-color: $color-surface;
  // }

  // 768
  @media (min-width: $bp-tablet) {
    &__header {
      margin-bottom: 40px;
    }

    &__list {
      gap: 30px;
    }

    &__card {
      display: grid;
      grid-template-columns: minmax(0, 1fr) minmax(0, 1fr);
      min-height: 316px;
    }

    &__media {
      height: 100%;
      min-height: 316px;
    }

    &__body {
      height: 100%;
      max-height: 316px;
      padding-top: 30px;
      padding-left: 40px;
    }

    &__card-title {
      line-height: 1.25;
      max-width: 20ch;
      margin-bottom: 16px;
    }

    &__excerpt {
      max-width: 38ch;
    }
  }

  // 1024
  @media (min-width: $bp-laptop-md) {
    padding-block: 100px 90px;

    &__card {
      grid-template-columns: minmax(420px, 1fr) 1fr;
    }

    &__title {
      font-size: 48px;
    }

    &__lead {
      max-width: 531px;
    }

    &__card-title {
      font-size: 32px;
      max-width: 340px;
    }

    &__excerpt {
      max-width: 340px;
    }

    &__item:first-child &__more {
      display: none;
    }
  }

  // 1440
  @media (min-width: $bp-laptop) {
    padding-block: 120px;

    &__header {
      max-width: 704px;
      margin-bottom: 48px;
    }

    &__list {
      gap: 32px;
    }

    &__item:first-child &__more {
      display: inline-flex;
    }

    &__card {
      grid-template-columns: clamp(560px, 40vw, 600px) minmax(0, 1fr);
    }

    &__media {
      height: auto;
      min-height: 316px;
      aspect-ratio: 560 / 316;
      max-width: 560px;
    }

    &__media picture {
      height: 100%;
    }

    &__body {
      padding: 45px 40px 40px 25px;
      width: 100%;
      min-height: 0;
    }

    &__card-title {
      max-width: 586px;
      letter-spacing: -0.01em;
    }

    &__excerpt {
      max-width: 557px;
    }
  }

  // 1920
  @media (min-width: $bp-desktop) {
    padding-block: clamp(120px, 6vw, 160px);

    &__header {
      max-width: 72ch;
      margin-bottom: clamp(40px, 3vw, 72px);
    }

    &__title {
      font-size: clamp(48px, 2.6vw, 56px);
      line-height: 1.15;
    }

    &__lead {
      font-size: 16px;
      line-height: 1.6;
      max-width: 62ch;
    }

    &__list {
      gap: clamp(32px, 3.5vw, 80px);
    }

    &__card {
      grid-template-columns: clamp(600px, 42vw, 760px) minmax(0, 1fr);
      min-height: clamp(316px, 18vw, 420px);
    }

    &__media {
      max-width: none;
      min-height: clamp(316px, 18vw, 420px);
      aspect-ratio: 760 / 420;
    }

    &__body {
      padding: clamp(40px, 3.2vw, 64px);
    }

    &__card-title {
      font-size: clamp(32px, 2vw, 40px);
      line-height: 1.2;
      max-width: 26ch;
      margin-bottom: clamp(16px, 1.4vw, 24px);
    }

    &__excerpt {
      font-size: 16px;
      line-height: 1.6;
      max-width: 56ch;
    }

    &__more {
      margin-top: clamp(24px, 2vw, 36px);
      font-size: 16px;
      line-height: 1.6;
    }
  }
}
